/* 
=======
skelenton loader
===============
*/


.skelenton{
  --surface:hsl(0 0% 90%);
  --block:rgb(254, 254, 254);
  --padding:1rem;
  --loader: hsl(35 100% 80%);
  padding: var(--padding)  ;
  border-radius: calc(var(--padding) * 1.5);
  margin: calc(var(--padding)*1) var(--padding) ;
  grid-gap: calc(var(--padding)*0.7);
  gap: calc(var(--padding)*0.7);
  display: grid;
  grid-template: 1fr 1fr / 25% 1fr;
  background:var(--surface);
  position: relative;
  
  padding-right:calc(var(--padding)*3);
  
  }
 
 
  .skelenton_block{
  background:linear-gradient(
      -75deg,
      transparent 40%,
      var(--loader),
      transparent 60%
      ) 0 0 / 200% 100%,
       var(--block);
  border-radius:calc(var(--padding)*0.5); 
  -webkit-animation: load 2s infinite linear; 
          animation: load 2s infinite linear;
  background-attachment: fixed;
  }
  @-webkit-keyframes load {
  to{
      background-position:200% 0 ;
  }
  }
  @keyframes load {
  to{
      background-position:200% 0 ;
  }
  }
  
  .skelenton_block:first-of-type{
  grid-row: 1 / -1;
  aspect-ratio: 1;
  }
  .skelenton_block:last-of-type{
  width: 65%;
  }
  .close-search{
    position: absolute; 
    right: 10px;
    top: 35px;
  }
  .s{
    padding: 2px 4px;
  }
  .s{
    font-weight: 500px;
  }
  
  .s:last-of-type{
    color: rgb(71, 71, 71);
  }
  .serach-img-div{
    display: flex;
  }
  .post-profile-search{
    border: white 2px solid;
    width:65px;
    height:65px;
    border-radius: 50px;
  }
  /* 
  =======
  skelenton loader end
  ===============
  */
